"use client";

import { useState } from "react";
import { Button } from "~/components/ui/button";
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from "~/components/ui/card";
import { Input } from "~/components/ui/input";
import { Alert, AlertDescription } from "~/components/ui/alert";

interface LoginError {
  error: string;
}

export default function Home() {
  const [error, setError] = useState<string | null>(null);
  const [isLoading, setIsLoading] = useState(false);

  async function onSubmit(formData: FormData) {
    setIsLoading(true);
    setError(null);

    try {
      const response = await fetch("/api/login", {
        method: "POST",
        body: formData,
      });

      if (!response.ok) {
        const data = await response.json() as LoginError;
        setError(data.error);
        return;
      }

      window.location.href = response.headers.get("Location") ?? "/";
    } catch (err) {
      setError("An error occurred. Please try again.");
    } finally {
      setIsLoading(false);
    }
  }

  return (
    <main className="flex min-h-screen flex-col items-center justify-center p-4">
      <div className="w-full max-w-md space-y-8">
        <div className="text-center space-y-2">
          <h1 className="text-4xl font-bold tracking-tight text-white">
            ARG Game
          </h1>
          <p className="text-slate-400">
            Immersive puzzle-solving in the real world
          </p>
        </div>

        <Card className="border-slate-800 bg-slate-900/50 backdrop-blur-sm">
          <CardHeader className="space-y-1">
            <CardTitle className="text-2xl text-center">Team Login</CardTitle>
            <CardDescription className="text-center">
              Enter your team code to start playing
            </CardDescription>
          </CardHeader>
          <CardContent>
            <form action={onSubmit} className="space-y-4">
              {error && (
                <Alert variant="destructive" className="bg-red-900/50 border-red-900">
                  <AlertDescription>{error}</AlertDescription>
                </Alert>
              )}
              <div className="space-y-2">
                <label
                  htmlFor="code"
                  className="text-sm font-medium leading-none text-slate-200"
                >
                  Team Code
                </label>
                <Input
                  id="code"
                  name="code"
                  type="text"
                  placeholder="Enter your team code"
                  required
                  className="bg-slate-800/50 border-slate-700 text-slate-200 placeholder:text-slate-500"
                  disabled={isLoading}
                />
              </div>
              <Button
                type="submit"
                className="w-full bg-blue-600 hover:bg-blue-700"
                disabled={isLoading}
              >
                {isLoading ? "Checking..." : "Start Playing"}
              </Button>
            </form>
          </CardContent>
          <CardFooter className="flex justify-center border-t border-slate-800 bg-slate-900/30">
            <Button variant="ghost" className="text-slate-400 hover:text-white" asChild>
              <a href="/admin">Admin Login</a>
            </Button>
          </CardFooter>
        </Card>
      </div>
    </main>
  );
}
